.intro{
    background-color: white;
    display: flex;
   
 
}

.left-intro{
    flex:0.2;
    background-color: aqua;
    position: relative;
    top: 30px;
    left: 60px;
    
}
.right-intro{
    flex:0.8;
    margin-top: 50px;
    background-color: pink;
    z-index:1;
    overflow: hidden;
    

}
.wrapper{
    height: 100%;
    width: 100%;
    padding-left: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

h1{
    font-size: 60px;
    margin: 10px 0;
}
h2{
    font-size: 35px;
}
h3{
    font-size: 30px;

}
span{
    color:aqua;
    background-color: #333;
}

.ityped-cursor {

    animation: blink 1.2s infinite;
    
}

@keyframes blink {
    50%{
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}